@charset "UTF-8";
body, #root, .App, .Calculator {
  width: 100%;
  height: 100%; }

.App {
  display: flex;
  align-items: center;
  justify-content: center; }

*,
*::before,
*::after {
  outline: 0;
  margin: 0;
  border: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: sans-serif; }

html {
  width: 100vw;
  height: 100vh; }

input {
  cursor: pointer;
  font-size: 1rem;
  color: #5fcc9c;
  background: #232855;
  border: 2.5px solid #215b63;
  border-radius: 5px;
  transition: 0.35s ease-in-out;
  transition-property: color, border, background; }
  input:hover {
    color: #ffffe3;
    border-color: #5fcc9c; }
  input:focus {
    outline: 0; }
  input:focus, input:active {
    color: #ffffe3;
    border-color: #ffffe3;
    background: #215b63; }

output {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  padding: 10px;
  color: #232855;
  background: #5fcc9c;
  border: 2.5px solid #215b63;
  border-radius: 5px; }

.App {
  overflow: hidden;
  padding: 10px;
  background-color: #232855; }

.Calculator {
  padding: 10px; }

.Equation {
  grid-area: equation; }

input[value='sin'] {
  grid-area: sin; }

input[value='cos'] {
  grid-area: cos; }

input[value='tan'] {
  grid-area: tan; }

input[value='asin'] {
  grid-area: asin; }

input[value='acos'] {
  grid-area: acos; }

input[value='atan'] {
  grid-area: atan; }

input[value='sinh'] {
  grid-area: sinh; }

input[value='cosh'] {
  grid-area: cosh; }

input[value='tanh'] {
  grid-area: tanh; }

input[value='asinh'] {
  grid-area: asinh; }

input[value='acosh'] {
  grid-area: acosh; }

input[value='atanh'] {
  grid-area: atanh; }

input[value='0'] {
  grid-area: zero; }

input[value='1'] {
  grid-area: one; }

input[value='2'] {
  grid-area: two; }

input[value='3'] {
  grid-area: three; }

input[value='4'] {
  grid-area: four; }

input[value='5'] {
  grid-area: five; }

input[value='6'] {
  grid-area: six; }

input[value='7'] {
  grid-area: seven; }

input[value='8'] {
  grid-area: eight; }

input[value='9'] {
  grid-area: nine; }

input[value='+'] {
  grid-area: add; }

input[value='-'] {
  grid-area: min; }

input[value='÷'] {
  grid-area: div; }

input[value='×'] {
  grid-area: mul; }

input[value='^'] {
  grid-area: pow; }

input[value='='] {
  grid-area: equal; }

input[value='.'] {
  grid-area: dot; }

input[value='±'] {
  grid-area: sign; }

input[value='C'] {
  grid-area: reset; }

input[value='CE'] {
  grid-area: clear; }

.Message {
  grid-area: message; }

.Result {
  grid-area: result; }

.basic {
  display: grid;
  grid-template-rows: repeat(6, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  grid-template-areas: 'result result result result' 'pow reset clear div' 'seven eight nine mul' 'four five six min' 'one two three add' 'sign zero dot equal'; }

.functions {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-area: 'functions';
  grid-template-areas: 'sin cos tan' 'asin acos atan' 'sinh cosh tanh' 'asinh acosh atanh'; }

.operands {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 10px;
  grid-area: 'operands';
  grid-template-areas: 'seven eight nine' 'four five six' 'one two three' 'zero zero zero'; }

.operators {
  display: grid;
  grid-template-rows: repeat(5, 1fr);
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
  grid-area: 'operators';
  grid-template-areas: 'reset clear' 'pow sign' 'div mul' 'min add' 'dot equal'; }
